nav > li {
  float: right;
  position: relative;
  cursor: pointer;
  list-style-type: none;
  margin-left: 33px;
  margin-right: 33px;
  border: 2px solid salmon;
  transition: margin .1s, padding .1s, border 1s;
}

nav > li:first-child {
  margin-left: 20px;
}

nav > li:before {
  right: 100%;
}
nav > li:after {
  left: 100%;
}

nav > li:hover {
  margin-top: -10px;
  box-shadow: 0 4px 12px 0px burlywood;
}

nav > li:hover:before, li:hover:after {
  height: 80%;
  background: cyan;
  transition: all .2s;
}

nav > li:hover:after {
  transform: rotate( 360deg ) skew( 30deg ) translate( -2px, 9px );
}

/* -- CONTAINERS -- */

.Flex-container {
  /*text-align: center;*/
  font-size: larger;
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-left: 10%;
  margin-right: 10%;
  background-color: #1f0047;
  color: chartreuse;
}

.Flex-container-left {
  font-size: larger;
  align-items: flex-start;
  display: flex;
  flex-direction: column;
