}

/* -- GAME STUFF -- */

#canvas {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}

img[id="YYYYYY"] {
  cursor: pointer;
  border-radius: 10px;
  margin-top: 15px;
}

/* -- TEXT -- */

p:hover {
  filter: invert();
}

#bigText {
  font-size: 40px;
  font-family: Impact;
  font-style: oblique;
  color: black;
  background-color: aqua;
}

#preText {
  font-size: 18px;
  padding: 20px;
}

#papyrus {
  font-weight: 900;
  opacity: 0.6;
}

/* * * -- INPUTS -- * * */

button:hover {
  transform: scale( 1.2 );
}

/*button:active {
  animation: spin 0.2s linear;
}*/

button {
  font-size: larger;
  padding: 20px;
  /*width: 200px;*/
  margin-top: 10px;
  margin-bottom: 10px;
  border-style: outset;
  border-width: 3px;
  border-radius: 10px;
  background-color: black;
  color: chartreuse;
  border-color: chartreuse;
}

button.YYY {
  border-color: #379000;
  color: #123dfb;
  background: #a2ff96;
  animation: pulse infinite 1s linear alternate;
}

button.more {
  color: #623afb;
  background: #f8cf66;
  animation: rainbow infinite 1s linear alternate-reverse;
}

button.tellMore {
  background-color: black;
  color: white;
}

/* -- MEDIA -- */

img[alt="256hen"], img[alt="256me"] {
  border-radius: 25px;
  border-color: greenyellow;
  border-style: inset;
  border-width: 2px;
  position: relative;
  display: inline-flex;
  filter: contrast( 1.25 ) saturate( 1.1 );
  transition: all 0.3s;
}

img[alt="256me"] {
  height: 175px;
}

img[alt="logo"] {
  animation: spin infinite 20s linear alternate-reverse;
}

img:hover {
  filter: contrast( 10 ) saturate( 10 );
}

/* -- MISC -- */

#clickable {
  cursor: pointer;
}

.flashBG {
  animation: flash-bg 500ms linear infinite alternate;
  border-radius: 5%;
  align-content: stretch;
}

.flashFG {
  animation: flash-fg 200ms linear infinite alternate;
}

.fixed {
  position: fixed;
  align-self: flex-start;
  /*
  inline-size: 125vh;
  block-size: 50vw;
  */
} 

.fixedCenter {
  position: fixed;
  transform: translateY( 50vh );
}

.hugCorners20 {
  animation: hug-corners 20s linear infinite;
} 
.hugCorners21 {
  animation: hug-corners 21s linear infinite;
} 
.hugCorners22 {
  animation: hug-corners 22s linear infinite;
} 
.hugCorners23 {
  animation: hug-corners 23s linear infinite;
}

.orbit {
  /*margin-left: 50%;
  margin-right: 50%;*/
  animation: orbit 20s linear infinite;
}

/* -- ANIMATIONS -- */

@keyframes rainbow {
  0% {
    background-color: red;
    color: green;
  }
  25% {
    background-color: yellow;
    color: red;
  }
  50% {
    background-color: blue;
    color: yellow;
  }
  100% {
    background-color: green;
    color: red;
  }
}

@keyframes pulse {
  from { transform: scale( 0.5 ); }
  to { transform: scale( 1.5 ); }
}

@keyframes orbit {
  from { transform: rotate( 0deg ) translateX( 950px ) rotate( 0deg ); }
  to { transform: rotate( 360deg ) translateX( 950px ) rotate( -360deg ); }
}

@keyframes hug-corners {
  0% { transform: translate( 0vw, 0vh ) }
  25% { transform: translate( 750vw, 0vh ) }
  50% { transform: translate( 750vw, 85vh ) }
  75% { transform: translate( 0vw, 85vh ) }
  100% { transform: translate( 0vw, 0vh ) }
}

@keyframes awkward-slide {
  0% { transform: translateX(-200px) }
  25% { transform: translateX(-100px) }
  50% { transform: translateX(100px) }
  75% { transform: translateX(-100px) }
  100% { transform: translateX(-200px) }
}

@keyframes flash-bw {
  from {
    color: white;
    background-color: black;
  }
  to {
    color: black;
    background-color: white;
  }
}

@keyframes flash-fg {
  from { color: black; }
  to { color: blue; }
}

@keyframes flash-bg {
  from { background-color: rgba(0, 255, 255, 0); }
  to { background-color: rgba(0, 255, 34, 0.466); }
}

@keyframes spin {
  from {
    transform: rotate( 0deg );
  }
  to {
    transform: rotate( 360deg );
  }
}
